.design-heading {
  color: white;
  font-family: "Courgette", cursive;
  font-weight: 900;
  font-size: 50px;
}
.btn1 {
  background-color: #f38487;
  border: 2px solid #e7717d;
  color: white;
}
.btn1:hover {
  background-color: #e7717d;
  border: 2px solid #e7717d;
  color: white;
}
label {
  color: white;
  font-size: 20px;
}
#counter {
  margin: 2%;
  display: flex;
}
.flex1 {
  flex: 1;
  cursor: default !important;
}
i {
  cursor: pointer;
}
td {
  background-color: rgba(255, 255, 255, 0.4);
  color: black;
}
.alert-danger {
  background-color: rgba(223, 35, 35, 0.5);
}
.fa-trash-alt:hover {
  color: maroon;
}
.fa-edit:hover {
  color: lightskyblue;
}
.show {
  display: block;
}
.hide {
  display: none;
}
#sortDiv {
  margin-top: 2%;
}
#flex-cont {
  display: flex;
}
#cont1,
#cont2 {
  flex: 1;
  margin: 2%;
}
