#heading h1 {
  text-align: center;
  font-size: 60px;
  font-weight: 900;
  font-family: "Pacifico", cursive;
  color: white;
  margin: 2% 0% 1% 0%;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
}
.options {
  display: flex;
}
.option-img {
  width: 80%;
  cursor: pointer;
}
.option {
  flex: 1;
  margin: 1% 5%;
}
.option-card {
  background: rgb(180, 115, 57);
  background: radial-gradient(
    circle,
    rgba(180, 115, 57, 1) 47%,
    rgba(135, 74, 16, 1) 100%
  );
  background-repeat: no-repeat;
  text-align: center;
  padding: 2%;
  width: 100%;
  height: 120%;
}
.circle-text {
  color: white;
  font-weight: 900;
  font-family: "Pacifico", cursive;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
}
#f1_container {
  position: relative;
  margin: 10px auto;
  width: 450px;
  height: 281px;
  z-index: 1;
}
.btn-design {
  background: rgb(252, 176, 69);
  background: linear-gradient(
    90deg,
    rgba(252, 176, 69, 1) 0%,
    rgba(218, 165, 32, 0.8687850140056023) 100%
  );
  background: rgba(58, 166, 90, 1);
  padding: 3% 10%;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  border: 3px solid rgb(0, 100, 0);
  border-radius: 12px;
  color: white;
  outline: none;
  margin: 3% 0%;
}
#f1_container {
  perspective: 1000;
}
#f1_card {
  width: 100%;
  height: 70%;
  transform-style: preserve-3d;
  transition: all 1s linear;
}
#f1_container:hover #f1_card {
  transform: rotateY(180deg);
}
.face {
  position: absolute;
  width: 100%;
  height: 133%;
  backface-visibility: hidden;
}
.face.back {
  display: block;
  transform: rotateY(180deg);
  box-sizing: border-box;
  padding: 16% 6%;
  color: white;
  text-align: center;
  background: rgb(191, 204, 191);
  background: linear-gradient(
    180deg,
    rgba(191, 204, 191, 1) 0%,
    rgba(255, 255, 255, 1) 100%
  );
}
