.shadow{
    background-color: rgba(0,0,0,0.5);
    padding: 3% 0%;
    margin-top: 1%;
}
.question-cont{
    background: rgb(121,162,61);
    background: radial-gradient(circle, rgba(121,162,61,1) 0%, rgba(61,115,35,1) 86%);
    padding:  1%;
    margin: 2%;
    border: 3px solid rgba(61,115,35,1);
    border-radius: 15px;
    color: white;
    font-size: 20px;
}
.hide{
    display: none;
}
.show{
    display: block;
}
.options-con{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 2%;
}
.options-con > button{
    display: flex; 
    flex-basis: calc(50% - 40px);  
    justify-content: center;
    flex-direction: column;
}
.options-con >button>div{
    display: flex;
    justify-content: center;
    flex-direction: row;
}

.question-option{
    display: flex;
    background-color: #C79C70;
    background: rgb(180,115,57);
    background: radial-gradient(circle, rgba(180,115,57,1) 47%, rgba(135,74,16,1) 100%);
    background: rgb(230,135,45);
    background: radial-gradient(circle, rgba(230,135,45,0.8547794117647058) 2%, rgba(230,103,45,1) 83%);
    color:white;
    padding:1%;
    margin:1%;
    border-radius: 15px;
    font-size: 18px;
    outline: none;
    text-align: left;
    border: 3px solid rgba(230,103,45,1);
    cursor: pointer;
}
.option-text{
    flex:9;
    text-align: left;
    margin:auto 0%;
}
.regular-checkbox {
	-webkit-appearance: none;
	background-color: #fafafa;
	border: 1px solid #4DB1FF;
	box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05);
	padding: 9px;
	border-radius: 3px;
	display: inline-block;
	position: relative;
}
.check-cont{
    display: inline;
    flex: 1;
}
.check-cont input[type="checkbox"]{
    padding: 15px;
    font-size: 20px;
}
.regular-checkbox:active, .regular-checkbox:checked:active {
	box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px 1px 3px rgba(0,0,0,0.1);
}

.regular-checkbox:checked {
	background-color: #e9ecee;
	border: 1px solid #adb8c0;
	box-shadow: 0 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05), inset 15px 10px -12px rgba(255,255,255,0.1);
	color: #99a1a7;
}
.regular-checkbox:checked:after {
	content:'\2713';
    font-size: 20px;
    font-weight: 900;
	position: absolute;
	top: 0px;
	left: 6px;
	color: #2A416F;
}
.img-remain-cont{
    display:flex;
}
.gif-cont{
    flex:1;
    margin-top: 0%;
}
.gif-cont img{
    width:100%;
}
.remain-cont{
    flex:4;
}
.btn-cont{
    display: flex;
    margin:0% 2%;
}
.btn-cont a ,.btn-cont button,.btn-cont img{
    flex:1;
    margin: 0% 6%;
}
.btn-cont img{
    width: 1%;
}
.button-design{
    padding: 10px;
    outline: none;
    font-size: 25px;
    font-weight: 900;
    color:white;
    border-radius: 12px;
    width: 100%;
    cursor: pointer;
}
.red-back{
    background: rgb(255,46,0);
    background: radial-gradient(circle, rgba(255,46,0,0.8603816526610644) 47%, rgba(255,0,0,0.9864320728291317) 100%);
    border: 5px solid red;
}
.yellow-back{
    border: 5px solid rgba(255,192,0,0.9864320728291317);
    background: rgb(255,235,0);
    background: radial-gradient(circle, rgba(255,235,0,0.8603816526610644) 47%, rgba(255,192,0,0.9864320728291317) 100%);
}
.disabled
{
    background: rgb(191,204,191);
background: radial-gradient(circle, rgba(191,204,191,1) 47%, rgba(147,158,147,1) 100%);
border: 5px solid rgba(147,158,147,1);
}
.scorecard{
    background: rgb(180,115,57);
    background: radial-gradient(circle, rgba(180,115,57,1) 47%, rgba(135,74,16,1) 100%);
    width:30%;
    margin: 3% auto;
    padding: 0% ;
    text-align: center;
}
.score-img{
    background-color: rgba(0,0,0,0.5);
    text-align: center;
    margin: 8% 10%;
}
.scorecard-heading{
    background: rgb(121,162,61);
    background: radial-gradient(circle, rgba(121,162,61,1) 0%, rgba(61,115,35,1) 86%);
    border-bottom: 5px solid rgba(61,115,35,1);
    padding: 2%;
}
.score-img img{
    width:50%;
}
.red{
    color: red !important;
}
.score-text-design{
    color:white;
    font-family: 'Permanent Marker',cursive;
    text-shadow: -5px 0 #8E8741, 0 5px #8E8741, 5px 0 #8E8741, 0 -5px #8E8741;
}
.line {
    width: 125px;
    border-bottom: 5px solid white;
    border-radius: 6px;
    position: absolute;
    left: 35%;
    margin-bottom: 4px;
}
.score-label {
    display: inline;
    text-shadow: -5px 0 #C7493A, 0 5px #C7493A, 5px 0 #C7493A, 0 -5px #C7493A;
}
.display-score{
    position: relative;
}
.display-score h1{
    margin:  0px;
} 
.scorecard-btn-cont{
    padding: 3% 0%;
    border:none;
    border-top: 5px solid rgba(61,115,35,1);
}
.scorecard-btn-cont .red-back{
    color:white;
}