header {
  background-color: #3aa65a;
  background: rgb(0, 100, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 100, 0, 1) 32%,
    rgba(58, 166, 90, 1) 100%
  );
  color: white;
  padding: 0.5em;
  display: flex;
  border-bottom: 8px solid darkgreen;
}
#logo {
  text-align: center;
  flex: 5;
}
#logo img {
  width: 30%;
}
#logo h5 {
  text-align: center;
  display: inline;
}
#menu {
  text-align: left;
  padding: 0.5em;
  flex: 3;
}
#menu a {
  text-decoration: none;
  margin: 0.6em;
  font-size: 20px;
}
#menu input {
  outline: none;
  margin-left: 0.6em;
  cursor: pointer;
}
#menu input[type="search"] {
  background: #ededed
    url(https://static.tumblr.com/ftv85bp/MIXmud4tx/search-icon.png) no-repeat
    9px center;
  border: solid 2px white;
  padding: 6px 6px 6px 32px;
  width: 20px;
  -webkit-border-radius: 8em;
  -moz-border-radius: 8em;
  border-radius: 8em;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
#menu input[type="search"]:focus {
  width: 200px;
  background-color: #fff;
  border-color: #66cc75;
  -webkit-box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
  -moz-box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
  box-shadow: 0 0 5px rgba(109, 207, 246, 0.5);
}
#menu input:-moz-placeholder {
  color: #999;
}
#menu input::-webkit-input-placeholder {
  color: #999;
}

#menu #create {
  position: relative;
}
#menu #create:before {
  position: absolute;
  content: "";
  height: 35px;
  border: 1px solid white;
  left: -13px;
}
#menu #create:after {
  position: absolute;
  content: "";
  height: 35px;
  border: 1px solid white;
  right: -13px;
}
#social-media {
  padding: 0.5em;
  text-align: right;
  flex: 2;
}
#social-media a {
  margin: 0.3em;
}
#social-media i {
  border: 2px solid white;
  border-radius: 50%;
  text-align: center;
  font-weight: 900;
  padding: 4%;
  color: #515151;
  background-color: #ededed;
}
#social-media .fa-facebook-f {
  padding: 4% 5%;
}
#social-media i:hover {
  color: white;
  background-color: blue;
}
#social-media .fa-instagram:hover {
  background-color: darkred;
}
